@import url(https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:wght@400;500;700&display=swap);
.Search {
  margin-top: 1rem;
  width: 100%;
  display: flex;
  justify-content: center;
}

.Search__label,
.Search__button,
.Search__input {
  border: none;
  outline: none;
  font-size: 0.9rem;
  background-color: transparent;
  color: #fff;
  font-weight: 500;
}

.Search__label {
  border: 1px solid #f2f2f2;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  width: 75%;
  position: relative;
}

.Search__input {
  width: 100%;
  padding: 0.75rem 0rem 0.75rem 1.75rem;
  font-weight: 700;
}

.Search__input::-webkit-input-placeholder {
  color: #f2f2f2;
  opacity: 0.85;
}

.Search__input::placeholder {
  color: #f2f2f2;
  opacity: 0.85;
}

.Search__button {
  padding: 0.75rem;
  font-weight: 700;
}

.Search__button {
  background-color: #f37928;
  border: 1px solid #f37928;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  width: 25%;
  cursor: pointer;
}

.Search__warning {
  background-color: #f2f2f2;
  border-color: #f00;
  color: #f00;
}

.Search__warning-content {
  position: absolute;
  top: 0.75rem;
  left: 1.75rem;
}

.Header {
  padding: 2rem 3rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.Header__title {
  text-align: left;
  font-size: 2rem;
  margin-bottom: 1.5rem;
}

@media (max-width: 375px) {
  .Header {
    padding: 2rem;
  }
}

.Current {
  display: flex;
  justify-content: space-between;
  padding: 1rem 0;
}

.Current__info {
  text-align: left;
  display: flex;
}

.Current__weather-icon {
  width: 100px;
  height: 100px;
  -webkit-animation-name: sun;
          animation-name: sun;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes sun {
  0% {
    scale: 1;
  }
  50% {
    scale: 1.1;
  }
}

@keyframes sun {
  0% {
    scale: 1;
  }
  50% {
    scale: 1.1;
  }
}

.Current__list {
  font-size: 0.85rem;
  list-style: none;
  margin-left: 0.75rem;
}

.list__temperature {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
  transition: scale 0.3s, padding-left 0.3s;
}

.list__temperature:hover {
  scale: 1.5;
  padding-left: 0.75rem;
}

.list__temperature-symbol {
  font-weight: 500;
  font-size: 1rem;
}

.Current__other-info {
  text-align: right;
}

.other-info__city {
  font-size: 1.15rem;
  margin-bottom: 1rem;
}

.other-info__clouds {
  font-weight: normal;
  font-size: 0.9rem;
}
.Current {
  padding: 0 0 1.5rem 0;
}

.Forecast {
}

.Forecast__title {
  font-size: 0.9rem;
  text-align: center;
}

.Forecast__weather-icon {
  width: 60px;
  height: 60px;
}

.Forecast__weather-icon:hover {
  -webkit-animation-name: icons;
          animation-name: icons;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes icons {
  0% {
    position: relative;
    top: 0;
  }
  50% {
    position: relative;
    top: 0.25rem;
  }
  100% {
    position: relative;
    top: 0;
  }
}

@keyframes icons {
  0% {
    position: relative;
    top: 0;
  }
  50% {
    position: relative;
    top: 0.25rem;
  }
  100% {
    position: relative;
    top: 0;
  }
}

.Forecast__temperature {
  display: flex;
  justify-content: center;
}

.temperature__max {
  margin-right: 0.2rem;
  color: #f37928;
}

.temperature__min {
  margin-left: 0.2rem;
}

.Main {
  display: flex;
  flex-direction: column;
  color: #f2f2f2;
  padding: 2rem 3rem;
}

.Main__forecast {
  display: flex;
  justify-content: space-between;
  padding-top: 1.5rem;
}

.Main__no-location {
  padding: 1.5rem 0;
  text-align: center;
}

.Main__no-location-paragraph {
  text-align: center;
}

.Main--warning {
  -webkit-animation-name: show-warning;
          animation-name: show-warning;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
}

@-webkit-keyframes show-warning {
  0% {
    opacity: 0;
  }
  15% {
    opacity: 1;
  }
  85% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes show-warning {
  0% {
    opacity: 0;
  }
  15% {
    opacity: 1;
  }
  85% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@media (max-width: 375px) {
  .Main {
    padding: 2rem;
  }
}

.App {
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.45);
}

.App__container {
  max-width: 500px;
  min-height: 100vh;
  margin: 0 auto;
}

.loading {
  margin: 5rem auto;
  border: 8px solid #f2f2f2;
  border-top: 8px solid #f37928;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  -webkit-animation: spin 1.5s linear infinite;
          animation: spin 1.5s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

#react-weather-app {
  font-size: 16px;
  font-family: Be Vietnam Pro, sans-serif;
  color: #f2f2f2;
  background-image: url(/react-weather-app/static/media/background.51e516e3.jpg);
}

